import React from 'react';
import Card from './Card';
import data from '../cards.json';

const Cards = () => {
    return (
        <>
        {
            data.map(({lang, url, fcolor, scolor}) => {
                return <Card key={lang} lang={lang} url={url} fcolor={fcolor} scolor={scolor}/>
            })
        }
        </>
    )
}

export default Cards
